<template>
    <div class="pos" >
        <!-- Hello AwesomePos Demo -->
        <el-row>
          <el-col :span="7" class="post-order" id="order-list">
             <el-tabs>
                   <el-tab-pane label="点餐">
                   <el-table :data="tableData" border style="width:100%" >
                       <el-table-column prop="goodsName" label="商品"> </el-table-column>

                       <el-table-column prop="count" label="数量" width="50">

                       </el-table-column>
                       <el-table-column prop="price" label="金额" width="70">

                       </el-table-column>
                       <el-table-column  label="操作" width="100" >

                           <template slot-scope="scope">
                               <el-button type ="text" size="small" @click="delSingleGoods(scope.row)">删除</el-button>
                               <el-button type ="text" size="small" @click="addOrderList(scope.row)">增加</el-button>
                           </template>
                       </el-table-column>
                   </el-table>


                    <div class="totalDiv">
                        数量：{{totalCount}}   &nbsp; &nbsp; &nbsp; &nbsp;   金额：{{totalMoney}}
                    </div>
                    <div class="div-btn">
                      <el-button type="warning">挂单</el-button>
                      <el-button type="danger" @click="delAllGoods">删除</el-button>
                      <el-button type="success" @click="checkOut">结账</el-button>
                    </div>

                    </el-tab-pane>
                 <el-tab-pane label="挂单">
                     挂单
                 </el-tab-pane>
                 <el-tab-pane label="外卖">
                     外卖
                 </el-tab-pane>
             </el-tabs>


          </el-col>
          <el-col :span="17">
              <div class="often-goods">
                  <div class="title">常用商品</div>
                  <div class="often-good-list">
                      <ul>
                          <li v-for="goods in oftenGoods" :key="goods.goodsId" @click="addOrderList(goods)">
                              <span>{{goods.goodsName}}</span>
                              <span class="o-price">￥{{goods.price}}</span>
                          </li>
                      </ul>
                  </div>
              </div>

                <div class="goods-type" >
                    <div class="" >
                    <el-tabs>
                        <el-tab-pane label="汉堡">  
                            <div>
                            <ul class='cookList'>
                               <li v-for="goods in type0Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                                     <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                     <span class="foodName">{{goods.goodsName}}</span>
                                     <span class="foodPrice">￥{{goods.price}}元</span>
                               </li>
                              </ul>
                            </div>
                        </el-tab-pane>

                        <el-tab-pane label="小食">
                              <div>
                            <ul class='cookList'>
                               <li v-for="goods in type1Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                                     <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                     <span class="foodName">{{goods.goodsName}}</span>
                                     <span class="foodPrice">￥{{goods.price}}元</span>
                               </li>
                              </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="饮料">
                              <div>
                            <ul class='cookList'>
                               <li v-for="goods in type2Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                                     <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                     <span class="foodName">{{goods.goodsName}}</span>
                                     <span class="foodPrice">￥{{goods.price}}元</span>
                               </li>
                              </ul>
                            </div>
                        </el-tab-pane>
                        <el-tab-pane label="套餐">
                              <div>
                            <ul class='cookList'>
                               <li v-for="goods in type3Goods" :key="goods.goodsId" @click="addOrderList(goods)">
                                     <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                                     <span class="foodName">{{goods.goodsName}}</span>
                                     <span class="foodPrice">￥{{goods.price}}元</span>
                               </li>
                              </ul>
                            </div>
                        </el-tab-pane>
                    </el-tabs>
                    </div>
                </div>



          </el-col>
        </el-row>
    </div>
</template>

